html {
  scroll-padding-top: 60px; /* same as navbar height */
}


#home, #projects, #about {
  height: calc(100vh - 60px);
  box-sizing: border-box;
}

.rect {
  background-color: #EEEFEE;
  height: 150vh;
  width: 100%;
  position: absolute;
  top: 0vh; /* directly below the #home section */
  z-index: -1;
}

.flower {
  width: 15vw;
  padding: 1rem;
}

#home {
  background-color: #ffffff;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 20vh 0rem 0 4rem;
  gap: 1rem;  
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
}

#projects {
  background-color: #EEEFEE;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 5rem 0 0 4rem;
}

#about {
  background-color: #ffffff;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 5rem 0 0 4rem;
  box-sizing: border-box;
}

#contact {
  height: calc(450px - 60px);
  background-color: #909FA3;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  padding: 5rem 0 0 5rem;
}

.hello {
  font-family: 'Inter';
  font-weight: 300;
  font-size: 32pt;
  margin: 0;
  margin-left: 2rem; 
}

.projects, .about {
  font-family: 'Inter';
  font-weight: 300;
  font-size: 28pt;
  margin: 0;
  margin-left: 2rem;   
}

.intro {
  font-family: 'Inter';
  font-weight: 300;
  font-size: 13pt;
  color: #656565;
  margin-top: 2rem;
  margin-left: 2rem;
  max-width: 70vw;
  line-height: 1.8;
}


.contact {
  font-family: 'Inter';
  font-weight: 400;
  font-size: 36pt;
  color: rgb(255, 255, 255);
}


.project-images {
  display: flex;
  overflow-x: auto;
  justify-content: flex-start;      /* Center the whole row */
  align-items: flex-start;
  gap: 5rem;  
  padding: 3rem;
  box-sizing: border-box;
  padding-top: 5rem;
  width: 100%;
  /* flex-wrap: wrap; */
}

.project-images img {
  flex-shrink: 1;
  width: 30vw;
  max-width: 450px;
  height: auto;
  border-radius: 10px;
}

.project-item {
  display: flex;
  flex-direction: column;
  align-items: center; /* center image and text horizontally */
  max-width: 30vw;     /* keep same max width as images */
}

.p1-title, .p2-title, .p3-title {
  margin-top: 2rem;
  font-family: 'Inter';
  font-weight: 300;
  font-size: 12pt;
  color: #000000;
  text-align: center;
  max-width: 90%;
}

.p1-description, .p2-description, .p3-description {
  margin-top: 0.5rem;  /* space between image and text */
  font-family: 'Inter';
  font-size: 12pt;
  font-weight: 300;
  color: #656565;
  text-align: left;
  max-width: 90%;
}

.project-item img {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-item img:hover {
  transform: scale(1.03);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

.about-content {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding-left: 2rem;
  box-sizing: border-box;
  padding-top: 5rem;
}

.about-image {
  max-width: 500px;
  height: auto;
  border-radius: 10px;
}

.about-text {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  box-sizing: border-box;
  padding-left: 5rem;
  padding-top: 0rem;
}

.text-one {
  font-family: 'Inter';
  font-size: 13pt;
}

.text-two {
  font-family: 'Inter';
  font-weight: 300;
  line-height: 1.4;
  font-size: 13pt;
  color: #656565;
  width: 50vw;
}

.email-button{
  display: inline-block;
  margin-top: 1rem;
  padding: 0.75rem 5rem;
  background-color: #494945; 
  color: white;
  font-family: 'Inter';
  font-weight: 300;
  font-size: 16px;
  border: none;
  border-radius: 6px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.email-button:hover {
  background-color: #2b2b2b; /* darker on hover */
}

.resume-button{
  display: inline-block;
  margin-top: 1rem;
  padding: 0.75rem 5rem;
  background-color: #656565; 
  color: white;
  font-family: 'Inter';
  font-weight: 300;
  font-size: 16px;
  border: none;
  border-radius: 6px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.resume-button:hover {
  background-color: #494945; /* darker on hover */
}


.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
}

.modal {
  background: #ffffff;
  padding: 2rem 2.5rem;
  border-radius: 12px;
  max-width: 500px;
  width: 80%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  text-align: left;
  position: relative;
}

.modal h2 {
  font-family: 'Inter';
  font-weight: 400;
  font-size: 14pt;
  margin-bottom: 1rem;
}

.modal p {
  font-family: 'Inter';
  font-weight: 300;
  font-size: 12pt;
  color: #656565;
  line-height: 1.6;
}

.modal h2,
.github-button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

