.github-button {
  display: inline-block;
  margin-top: 2rem;
  padding: 0.5rem 1.75rem;
  background-color: #656565;
  color: white;
  font-family: 'Inter';
  font-size: 16px;
  font-weight: 300;
  border: none;
  border-radius: 6px;
  text-decoration: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.github-button:hover {
  background-color: #494945;
}

.github-button-wrapper {
  display: flex;
  justify-content: center;
}